<template>
  <div class="mapTable" >
    <div class="btnBox" v-show="!spotTable" @click="spotTable=true">
      <i-button icon="ios-chatboxes"></i-button>
    </div>
    <div class="modelBox" v-if="spotTable">
      <p>
        <strong :class="{ active : tabState === 0 }"  @click="switchTab(0)">
          拐点坐标
        </strong>
        <strong :class="{ active : tabState === 1 }" @click="switchTab(1)">
          红线台账
        </strong>
        <strong :class="{ active : tabState === 2 }" @click="switchTab(2)">
          界碑界桩
        </strong>
        <strong :class="{ active : tabState === 3 }" @click="switchTab(3)">
          标识牌
        </strong>
        <Button type="primary" size="small" v-if="tabState === 0" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportPointExcel" class="exportBtn">导出</Button>
        <Button type="primary" size="small" v-if="tabState === 2" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportPreExcel" class="exportBtn">导出</Button>
        <Button type="primary" size="small" v-if="tabState === 3" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportBoardPreExcel" class="exportBtn">导出</Button>
      </p>
      <!--拐点坐标-->
      <div class="table_box" v-if="tabState === 0">
        <Table :loading="loading1" border stripe :columns="columns1" :data="data1" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total1" :page-size="pageSize1" simple size="small" class="thePage" @on-change="pageChange1" :current="pageNum1" show-total></Page>
      </div>
      <!--红线台账-->
      <div class="table_box" v-if="tabState === 1">
        <Table :loading="loading2" border stripe :data="data2" :columns="columns2" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total2" :page-size="pageSize2" simple size="small" class="thePage" @on-change="pageChange2" :current="pageNum2" show-total></Page>
      </div>
      <!--界碑界桩-->
      <div class="table_box" v-if="tabState === 2">
        <Table  :loading="loading3" border stripe :columns="columns3" :data="data3" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total3" :page-size="pageSize3" simple size="small" class="thePage" @on-change="pageChange3" :current="pageNum3" show-total></Page>
      </div>
      <!--标识标牌-->
      <div class="table_box" v-if="tabState === 3">
        <Table :loading="loading4" border stripe :columns="columns4" :data="data4" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total4" :page-size="pageSize4" simple size="small" class="thePage" @on-change="pageChange4" :current="pageNum4" show-total></Page>
      </div>
    </div>
  </div>
</template>

<script>
import {
  /**
   * 拐点相关接口
   */
  getPointList,
  exportPointExcel,
  /**
   * 界碑界桩相关接口
   */
  getPreMarkerList,
  exportPreExcel,
  /**
   * 标识标牌相关接口
   */
  gePretLmBoardList,
  exportBoardPreExcel,
  /**
   * 红线相关接口
   */
  redlineData
} from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
import { cacuLonLat } from '@/utils/common'

export default {
  name: 'MapTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32,
      /* 列表框打开 */
      spotTable: true,
      /* 列表伸缩框打开 */
      telescopic: true,
      /**
       * 拐点相关参数
       */
      /* 原始界桩表头 */
      columns1: [
        {
          title: '拐点编码',
          key: 'lpCode',
          width: 180,
          fixed: 'left',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(params.row.lpLon, params.row.lpLat, params.row.lpId, 'inflectionPoint')
                  }
                }
              }, params.row.lpCode)
            ])
          }
        },
        {
          title: '经度',
          key: 'lpLon',
          minWidth: 120,
          align: 'center',
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.lpLon))
          }
        },
        {
          title: '纬度',
          key: 'lpLat',
          minWidth: 120,
          align: 'center',
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.lpLat))
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum1: 1,
      /* 每页条数 */
      pageSize1: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总条数 */
      total1: 0,
      /* 加载 */
      loading1: false,
      /**
       * 红线图斑相关参数
       */
      /* 加载 */
      loading2: false,
      /* 页码 */
      pageNum2: 1,
      /* 每页条数 */
      pageSize2: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总数 */
      total2: 0,
      /* 红线图斑数据 */
      data2: [],
      /* 红线台账表格列配置项 */
      columns2: [
        {
          title: '红线编码',
          key: 'srldNumber',
          align: 'center',
          width: 150,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd2(params.row.srldId)
                  }
                }
              }, params.row.srldNumber)
            ])
          }
        },
        {
          title: '功能属性',
          key: 'srldTarget',
          align: 'center',
          minWidth: 200
        },
        {
          title: '面积',
          key: 'srldArea',
          align: 'center',
          minWidth: 120
        }
      ],
      /**
       * 界碑界桩相关参数
       */
      /* 原始界桩表头 */
      columns3: [
        {
          title: '界桩编码',
          key: 'jzNumber',
          align: 'center',
          width: 180,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd3(params.row.longitude, params.row.latitude, params.row.id, 'inflectionJiebei')
                  }
                }
              }, params.row.jzNumber)
            ])
          }
        },
        {
          title: '界桩刻号',
          key: 'jzKh',
          align: 'center',
          minWidth: 180
        },
        {
          title: '所属红线斑块',
          key: 'redlineName',
          align: 'center',
          minWidth: 170
        },
        {
          title: '行政区划',
          key: 'placeName',
          align: 'center',
          minWidth: 150
        }
      ],
      /* 表格数据 */
      data3: [],
      /* 页码 */
      pageNum3: 1,
      /* 每页条数 */
      pageSize3: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总条数 */
      total3: 0,
      /* 加载 */
      loading3: false,
      /* 加载 */
      /**
       * 标识标牌相关参数
       */
      /* 标识牌表头 */
      columns4: [
        {
          title: '标识牌编码',
          key: 'number',
          align: 'center',
          width: 150,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd4(params.row.longitude, params.row.latitude, params.row.id, 'inflectionBiaopai')
                  }
                }
              }, params.row.number)
            ])
          }
        },
        {
          title: '所属红线斑块',
          key: 'redlineName',
          align: 'center',
          minWidth: 200
        },
        {
          title: '经度',
          key: 'longitude',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.longitude))
          }
        },
        {
          title: '纬度',
          key: 'latitude',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.latitude))
          }
        }
      ],
      /* 表格数据 */
      data4: [],
      /* 页码 */
      pageNum4: 1,
      /* 每页条数 */
      pageSize4: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总条数 */
      total4: 0,
      /* 加载 */
      loading4: false,
      /* tab选项卡状态 */
      tabState: 0
    }
  },
  props: {
    /* 行政区划 */
    obj: {
      default: {
        code: '630000',
        param: ''
      }
    },
    param: {},
    srId: null
  },
  components: {},
  watch: {
    // 根据查询条件显示不同复合条件的列表
    obj () {
      if (this.tabState === 0) {
        this.data1 = []
        this.getTableData()
      } else if (this.tabState === 1) {

      } else if (this.tabState === 2) {
        this.data3 = []
        this.getTableData3()
      } else if (this.tabState === 3) {
        this.data4 = []
        this.getTableData4()
      }
    },
    /**
     * 拐点相关参数
     */
    srId (val) {
      this.pageNum1 = 1
      this.getTableData()
    },
    pageNum1 () {
      this.getTableData()
    },
    spotTable (val) {
      this.$emit('mapSizeChange', val)
    },
    /**
     * 界碑界桩相关参数
     */
    pageNum3 () {
      this.getTableData3()
    },
    /**
     * 标识标牌相关参数
     */
    pageNum4 () {
      this.getTableData4()
    },
    /**
     * 红线
     */
    pageNum2 () {
      this.getRedlineData()
    }
  },
  created () {
    this.switchTab(this.tabState)
  },
  mounted () {
    /* 监听浏览器高度 */
    window.addEventListener('resize', () => {
      // 表格自适应高度
      this.tableHeight = Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32
      // 根据tab选项卡状态不同，不同表格页容量自适应数量
      this.pageSize1 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize2 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize3 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize4 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.switchTab(this.tabState)
    }, false)
  },
  methods: {
    /* 获取拐点坐标列表 */
    getTableData () {
      this.loading1 = true
      getPointList({
        accessToken: getToken(),
        validate: true,
        data: {
          pageNum: this.pageNum1,
          pageSize: this.pageSize1,
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total1 = res.data.data.total
        // 点击切换拐点坐标，将对应图层显示在地图
        this.$emit('getPointData', res.data.data.rows, 'inflectionPoint')
        this.loading1 = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /* 分页跳转 */
    pageChange1 (e) {
      this.pageNum1 = e
    },
    /* 导出拐点列表文件 */
    exportPointExcel () {
      exportPointExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * 界碑界桩相关方法
     */
    /* 获取预设界桩列表 */
    getTableData3 () {
      this.loading3 = true
      getPreMarkerList({
        accessToken: getToken(),
        validate: true,
        data: {
          pageNum: this.pageNum3,
          pageSize: this.pageSize3,
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        this.data3 = res.data.data.rows
        this.total3 = res.data.data.total
        // 点击切换界桩，将对应图层显示在地图
        this.$emit('getPointData', res.data.data.rows, 'inflectionJiebei')
        this.loading3 = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd3 (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /* 分页跳转 */
    pageChange3 (e) {
      this.pageNum3 = e
    },
    /* 导出预设界桩界桩列表文件 */
    exportPreExcel () {
      exportPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * 标识标牌相关方法
     */
    /* 获取标识牌列表 */
    getTableData4 () {
      this.loading4 = true
      gePretLmBoardList({
        data: {
          pageNumber: this.pageNum4,
          pageSize: this.pageSize4,
          boardNum: this.obj.param,
          code: this.obj.code
        }
      }).then((res) => {
        this.data4 = res.data.data.row
        this.total4 = res.data.data.total
        // 点击切换标识标牌，将对应图层显示在地图
        this.$emit('getPointData', res.data.data.row, 'inflectionBiaopai')
        this.loading4 = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd4 (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /* 分页跳转 */
    pageChange4 (e) {
      this.pageNum4 = e
    },
    /* 导出预设标识牌列表文件 */
    exportBoardPreExcel () {
      exportBoardPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取红线图斑数据 */
    getRedlineData () {
      this.loading2 = true
      redlineData({
        data: {
          pageNum: this.pageNum2,
          pageSize: this.pageSize2
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.total2 = res.data.data.total
          this.data2 = res.data.data.rows
          // 点击切换标识标牌，将对应图层显示在地图
          this.$emit('getPointData', res.data.data.rows, '')
          this.loading2 = false
        }
      })
    },
    /* 点击定位到红线斑块 */
    ondbclickTd2 (id) {
      this.$emit('clickRedLineTable', id)
    },
    /* 分页跳转 */
    pageChange2 (e) {
      this.pageNum2 = e
    },
    /* 切换拐点、红线、界碑界桩、标识标牌 */
    switchTab (state) {
      this.tabState = state
      if (state === 0) {
        this.getTableData()
      } else if (state === 1) {
        this.getRedlineData()
      } else if (state === 2) {
        this.getTableData3()
      } else if (state === 3) {
        this.getTableData4()
      }
    }
  }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
  .mapTable{
    height:100%
  }
  .btnBox{
    position: absolute;right:10px;top:10px;
  }
  .modelBox{
    background: #F5F5F5;
    height:100%;
    margin-right: -1px;
    p{
      padding:0 15px 0 0;
      position: relative;
      overflow: hidden;
      text-align: left;
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height:0;
      }
      strong{
        float: left;
        display: inline-block;
        cursor: pointer;
        height: 100%;
        font-size:14px;
        padding:5px 20px;
      }
      .active{
        border: 1px solid #DADCE0;
        background: #fff;
        border-bottom: 0;
      }
      span{
        float: right;
        cursor: pointer;
      }
    }
    .table_box{
      padding: 0;
      height: calc(100% - 34px);
      background: #fff;
      .thePage{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        text-align: right;
        background: #fff;
        padding: 5px 0;
        border: 1px solid #e8eaec;
      }
    }
  }
  .exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
</style>
